
const swiper = new Swiper('.swiper', {
    // Optional parameters
    direction: 'horizontal',
    slidesPerView: 3,
    spaceBetween: 30,
    // slidesPerGroup: 3,
    loop: true,
    loopFillGroupWithBlank: true,
    speed: 500,

    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },

    breakpoints: {
      // when window width is >= 320px
      300: {
        slidesPerView: 1.2,
        spaceBetween: 10,
        slidesPerGroup: 1,
      },

      450: {
        slidesPerView: 1.5,
        spaceBetween: 15,
      },

      640: {
        slidesPerView: 2.2,
        spaceBetween: 15,
      },

      800: {
        slidesPerView: 2.5,
        spaceBetween: 15,

      },
      960: {
        slidesPerView: 3.2,
        spaceBetween: 20,

      },
      1260: {
        slidesPerView: 3,
        spaceBetween: 30,
        slidesPerGroup: 3,
      },
    }

  });