


const btnForModal = document.querySelectorAll('.btn-for-modal');
const overlay1 = document.querySelector('.overlay1');
const modalCross = document.querySelector('.modal-cross');

const overlay2 = document.querySelector('.overlay2');
const overlay3 = document.querySelector('.overlay3');

const form = document.querySelectorAll('.form');

//Первая форма
const orderForm = document.querySelector('.order__form');
const formOrderName = document.querySelector('.form-order-name');
const formOrderPhone = document.querySelector('.form-order-phone');
//const orderFormBtn = document.querySelector('.order__form-btn');

//Вторая форма
const creditForm = document.querySelector('.credit__form');
const creditName = document.querySelector('.credit__name');
const creditPhone = document.querySelector('.credit__phone');

//Третья форма
const connectForm = document.querySelector('.connect-form');
const nameInputModal = document.querySelector('.name-input-modal');
const nameInputPhone = document.querySelector('.name-input-phone');

btnForModal.forEach(btn => {
    btn.addEventListener('click', (e) => {
        e.preventDefault();
        overlay1.classList.add('active');
        document.body.style.overflow = 'hidden';
    })
})

modalCross.addEventListener('click', () => {
    overlay1.classList.remove('active');
    document.body.style.overflow = '';
})

//Функция formSubmit
function formSubmit(form, input1, input2) {
        form.addEventListener('submit', (e) => {
            e.preventDefault();
            if(input1.value !== '' && input2.value !== '') {
                overlay2.classList.add('active');
                form.reset();
                overlay1.classList.remove('active');
                setTimeout(() => {
                    overlay2.classList.remove('active');
                    document.body.style.overflow = '';
                }, 3000)
                document.body.style.overflow = '';
            } else {
                overlay3.classList.add('active');
            }
        })
}

formSubmit(orderForm, formOrderName, formOrderPhone);
formSubmit(creditForm, creditName, creditPhone);
formSubmit(connectForm, nameInputModal, nameInputPhone);

//Закрытие модалок с оповещениями
const btnNotification = document.querySelectorAll('.btn-notification');
const overlay = document.querySelectorAll('.overlay');

btnNotification.forEach(btn => {
    btn.addEventListener('click', () => {
        overlay.forEach(item => {
            item.classList.remove('active');
        })
    })
})