
'use strict';

const wrp = document.querySelector('.wrp');
const headerSwitch = document.querySelectorAll('.header__switch');
const brandsImgItem = document.querySelectorAll('.brands__img-item');

const darkImg = document.querySelectorAll('.dark-img');
const whiteImg = document.querySelectorAll('.white-img');

const darkColor = document.querySelectorAll('.dark-color');

const darkSvg = document.querySelectorAll('.dark-svg');

headerSwitch.forEach(item => {
    item.addEventListener('click', () => {
        if(!wrp.classList.contains('active')) {
            wrp.style.cssText = `
                background-color: #0F0F0F;
            `;
            wrp.classList.add('active');
        } else {
            wrp.style.cssText = `
                background-color: transparent;
            `;
            wrp.classList.remove('active');
        }


        darkImg.forEach(item => {
            item.classList.toggle('active');
        })
        whiteImg.forEach(item => {
            item.classList.toggle('active');
        })

        darkColor.forEach(item => {
            item.classList.toggle('active');
        })


        darkSvg.forEach(item => {
            item.classList.toggle('active');
        })
    })

})


