
const headerBurger = document.querySelectorAll('.header__burger');
const headerMenu = document.querySelectorAll('.header__menu');



headerBurger[0].addEventListener('click', () => {
    headerMenu[0].style.transform = 'translateY(0)';
})
headerBurger[1].addEventListener('click', () => {
    headerMenu[1].style.transform = 'translateY(0)';
})
window.addEventListener('click', (e) => {
    if(!e.target.closest('.header__menu') && !e.target.closest('.header__burger')) {
        headerMenu[0].style.transform = 'translateY(-100%)';
        headerMenu[1].style.transform = 'translateY(-100%)';
    }
})
